*{
    box-sizing: border-box;
}


.comment {
    width: 100%;
    height: auto;
}

.comment-text-area {
    width: 100%;
    height: auto;
}

.textinput {
    margin: 0;
    padding: 0;
    float: left;
    width: 60%;
    min-height: 50vh;
    outline: none;
    resize: none;
    border: 1px solid grey;
}


#page {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    flex-wrap: nowrap;
}

#preview {
    border: 1px solid black;
    width: 60%;
    min-height: 50vh;
    max-height: 50vh;
    overflow-y: scroll;
}

#buttons-abs{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    right: 80%;
}

#button-abs{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 50%;
    right: 80%;
}

.btn-abs{
    cursor: pointer;
}

code {
    background-color: rgb(233, 233, 233);
    border-radius: 2px;
    
}

blockquote {
    border-left: 3px solid rgb(155, 207, 104);
    padding-left: 4px;
}

table {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  table td, table th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  table tr:nth-child(even){background-color: #f2f2f2;}
  
  table tr:hover {background-color: #ddd;}
  
  table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: rgb(155, 207, 104);
    color: white;
  }

pre {
    background-color: rgb(233, 233, 233);
    padding: 20px;
}

a{
    text-decoration: none;
    color: rgb(155, 207, 104);
}

a:hover {
    color: rgb(183, 228, 139);
  }